import {Component, Mixins} from "vue-property-decorator";
import {Validation as Validation_} from "vuelidate";


@Component
export default class Validation extends Mixins() {
    getFieldState(field: Validation_): boolean | null {
        if (!field.$dirty) return null

        return !field.$error
    }

    getValidationMessage(field: Validation_): string {
        // @ts-ignore
        if (field.$params.required && !field.required) return '   '
        // @ts-ignore
        else if (field.$params.email && !field.email) return '   '
        // @ts-ignore
        else if (field.$params.minValue && !field.minValue) return `     ${field.$params.minValue.min}`
        else return ''
    }
}
